import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import { useSelector } from 'react-redux';

import HomeNav from './HomeNavigation/HomeNav';
import HomePopup from './HomePopup/HomePopup';

import floorData from '../../Db/floor.json';

export default function SectionInfo() {
  const zoomState = useSelector((state) => state.zoomState.value);

  const [visible, setVisible] = useState('unvisible');

  useEffect(() => {
    if (zoomState) {
      setTimeout(() => {
        setVisible('fade-in');
      }, 2200);
    } else {
      setVisible('fade-out');
    }
  }, [zoomState]);

  return (
    <div className={`section-information ${visible}`}>
      <HomeNav />
      <HomePopup />
      <div className="floor-container">
        { floorData.floor.map((data, i) => (
          <div className="floors" key={data.value}>
            <div className="floor-number">
              <p className="body-medium">
                {data.number}
              </p>
            </div>
            <Link to="/home/description">
              <div
                className="floor-pin"
                role="presentation"
                onClick={() => {
                  localStorage.setItem('value', (floorData.floor.length - 1) - i);
                }}
              >
                <div className="pin-head" />
                <hr className="pin-line" />
                <div className="pin-icon">
                  <img src={process.env.PUBLIC_URL + data.icon} alt={data.number} />
                </div>
                <div className="pin-title">
                  <p className="body-medium">
                    {data.title}
                  </p>
                </div>
              </div>
            </Link>
          </div>
        ))}
      </div>
    </div>
  );
}
