/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';
import floorData from '../Db/floor.json';

export const valueGroup = createSlice({
  name: 'valueGroup',
  initialState: {
    totalFloorValue: floorData.floor.length - 1,
    moveValue: 2.5,
  },
});
export default valueGroup.reducer;
