import * as THREE from 'three';
import React from 'react';
import { useGLTF } from '@react-three/drei';
import { useFrame, useLoader } from '@react-three/fiber';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader';

import { useDispatch, useSelector } from 'react-redux';
import { setZoomStateTrue } from '../../../Store/zoomState';

import assets from '../../../Db/assets.json';

export default function Building() {
  const dispatch = useDispatch();
  const zoomState = useSelector((state) => state.zoomState.value);

  const { buildingModelUrl } = assets.models[0];
  const { buildingMaterialUrl } = assets.models[0];
  const buildingMaterial = useLoader(MTLLoader, process.env.PUBLIC_URL + buildingMaterialUrl);
  const building = useLoader(OBJLoader, process.env.PUBLIC_URL + buildingModelUrl, (loader) => {
    buildingMaterial.preload();
    loader.setMaterials(buildingMaterial);
  });

  const vec = new THREE.Vector3();

  useFrame((state) => {
    if (zoomState === true) {
      // state.camera.fov = THREE.MathUtils.lerp(state.camera.fov, 10, 0.04);
      state.camera.position.lerp(vec.set(0, 0, 70), 0.04);
      state.camera.lookAt(0, -1, 0);
      state.camera.updateProjectionMatrix();
    } else if (zoomState === false) {
      state.camera.position.lerp(vec.set(-60, 80, 100), 0.04);
      state.camera.lookAt(0, -1, 0);
    }
    return null;
  });

  return (
    <primitive
      object={building}
      scale={0.1}
      position={[0, 0, 0]}
      onClick={() => {
        dispatch(setZoomStateTrue());
      }}
    />
  /*
    <mesh
      onClick={() => {
        dispatch(setTrue());
      }}
    >
      <boxGeometry args={[4, 17.2, 4]} scale={0.2} />
      <meshStandardMaterial color="#333333" />
    </mesh>
    <group>

      />
    </group>
<group
      {...props}
      dispose={null}
      ref={buildingRef}
      position={[0, -6.5, 0]}
    >
      <mesh
        geometry={nodes.Cube002.geometry}
        material={materials['Material.002']}
        position={[0.58, 6.31, 0.46]}
        scale={[1.92, 8.5, 1.92]}
      >
        <meshStandardMaterial color="#3f3f3f" />
      </mesh>
      <Suspense fallback={null}>
        <primitive object={seesawBuilding} />
      </Suspense>
    </group>
    */
  );
}

useGLTF.preload('/building.glb');
