/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const zoomState = createSlice({
  name: 'zoomState',
  initialState: {
    value: false,
  },
  reducers: {
    setZoomStateTrue: (state) => {
      state.value = true;
    },
    setZoomStateFalse: (state) => {
      state.value = false;
    },
    zoomStateByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setZoomStateTrue,
  setZoomStateFalse,
  zoomStateByState,
} = zoomState.actions;

export default zoomState.reducer;
