import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
// import { Canvas } from '@react-three/fiber';
// import { OrbitControls } from '@react-three/drei';

import FloorInfo from '../Components/FloorInformation/FloorInfo';
import FloorMap from '../Components/FloorMap/FloorMap';
// import FloorNav from '../Components/FloorNavigation/FloorNav';

import FloorPopup from '../Components/FloorPopup/FloorPopup';
// import FloorUserPath from '../Components/FloorPopup/FloorUserPath';

// import FloorGroup from '../Models/FloorGroup';

export default function Floor() {
  const moveValue = useSelector((state) => state.valueGroup.moveValue);

  const [floorValue, setFloorValue] = useState(JSON.parse(localStorage.getItem('value')));
  const [floorHeight, setFloorHeight] = useState(floorValue * -moveValue);
  const [popup, setPopup] = useState(0);

  useEffect(() => {
    localStorage.setItem('value', floorValue);
    localStorage.setItem('floor Height', floorHeight);
  }, [floorValue, floorHeight]);

  return (
    <div
      className="floor"
      /*
      style={floorValue < 7 ? {
        background: '#8f8f8f',
      } : {
        background: '#333333',
      }}
      */
    >
      <FloorPopup
        floorValue={floorValue}
        setPopup={setPopup}
        popup={popup}
      />
      <FloorMap
        floorValue={floorValue}
        setPopup={setPopup}
      />
      <div className="floor-function-container">
        <FloorInfo
          setFloorValue={setFloorValue}
          setFloorHeight={setFloorHeight}
          setPopup={setPopup}
          floorValue={floorValue}
          floorHeight={floorHeight}
          popup={popup}
        />
        {/*
        <FloorNav
          setFloorValue={setFloorValue}
          setFloorHeight={setFloorHeight}
          floorValue={floorValue}
          floorHeight={floorHeight}
        />
        */}
      </div>
      {/*
      <FloorUserPath
        floorValue={floorValue}
      />
      <Canvas
        camera={{
          fov: 47,
          position: [0, 0, 0],
        }}
      >
        <FloorGroup
          setPopup={setPopup}
          floorValue={floorValue}
          floorHeight={floorHeight}
          popup={popup}
        />
        <pointLight position={[-10, 20, 20]} />
        <OrbitControls />
      </Canvas>
      */}
    </div>
  );
}
