import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setFadeTrue } from '../../Store/fadeState';

import facilityData from '../../Db/facility.json';

export default function FloorMap({
  setPopup,
  floorValue,
}) {
  const dispatch = useDispatch();
  const totalFloorValue = useSelector((state) => state.valueGroup.totalFloorValue);
  const currentFloor = Math.abs(floorValue - totalFloorValue);
  return (
    <div className="floor-map">
      {facilityData.floor.map((data, i) => (
        <div className={`floor-content ${currentFloor === i ? 'show-floor-content' : ''}`}>
          <img src={process.env.PUBLIC_URL + data.isometric} alt={data.title} />
          <div className="pins">
            {data.facilities?.map((el, num) => (
              <div
                className={`pin ${facilityData.floor[Math.abs(floorValue - totalFloorValue)].title}-pin-${num + 1}`}
                onClick={() => {
                  setPopup(num);
                  dispatch(setFadeTrue());
                }}
                role="presentation"
              >
                {el.icon ? (
                  <i className="pin-icon">
                    <img src={process.env.PUBLIC_URL + el.icon} alt={el.name} />
                  </i>
                ) : null}
                {el.name ? (
                  <p className="pin-title sub-heading">
                    {el.name}
                  </p>
                ) : null}
              </div>
            ))}
          </div>
        </div>
      ))}
    </div>
  );
}
