import React from 'react';
import { useLoader } from '@react-three/fiber';
import { OBJLoader } from 'three/examples/jsm/loaders/OBJLoader';
import { MTLLoader } from 'three/examples/jsm/loaders/MTLLoader';
import { useSelector } from 'react-redux';

import assets from '../../../Db/assets.json';

export default function City() {
  const zoomState = useSelector((state) => state.zoomState.value);
  const { cityModelUrl } = assets.models[0];
  const { cityMaterialUrl } = assets.models[0];

  const showCityMaterial = useLoader(MTLLoader, process.env.PUBLIC_URL + cityMaterialUrl);
  const showCity = useLoader(OBJLoader, process.env.PUBLIC_URL + cityModelUrl, (loader) => {
    showCityMaterial.preload();
    loader.setMaterials(showCityMaterial);
  });
  // const hideCity = useLoader(OBJLoader, process.env.PUBLIC_URL + cityModelUrl);
  /*
const { scale } = useSpring({
  scale: zoom !== true ? [100, 0.01, 100] : [1, 1, 1],
});
 */

  return (
    zoomState ? null : <primitive object={showCity} scale={0.1} />
  );
}
