import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { setHomePopupFalse } from '../../../Store/homePopupState';

import Button from '../../Button';
import assets from '../../../Db/assets.json';

export default function HomePopup() {
  const dispatch = useDispatch();
  const homePopupState = useSelector((state) => state.homePopupState.value);
  const [currentImage, setCurrentImage] = useState(0);
  return (
    <div className={`home-popup ${homePopupState ? 'show-home-popup' : ''}`}>
      <Button
        icon={assets.icons[0].closeBlack}
        clickEvent={() => {
          dispatch(setHomePopupFalse());
          setCurrentImage(0);
        }}
        buttonName="popup-close-button"
      />
      <div className="image-gallery">
        <ul className="image-list">
          {assets.renders.map((data, i) => (
            <li
              className={`body-small list-item ${currentImage === i ? 'list-item-selected' : ''}`}
              key={data.title}
              role="presentation"
              onClick={() => {
                setCurrentImage(i);
              }}
            >
              {`${i + 1}. ${data.title}`}
            </li>
          ))}
        </ul>
        <div className="image-area">
          <img
            src={process.env.PUBLIC_URL
                + assets.renders[currentImage].render}
            alt={assets.renders[currentImage].title}
          />
        </div>
      </div>
    </div>
  );
}
