import React, { Suspense } from 'react';
import { Canvas } from '@react-three/fiber';
import { OrbitControls } from '@react-three/drei';

import City from '../Components/BuildingOverview/Models/City';
import Building from '../Components/BuildingOverview/Models/Building';

import FloorOverview from '../Components/BuildingOverview/FloorOverview';

export default function Home() {
  return (
    <div className="home">
      <div className="model-container">
        <Canvas
          camera={{
            fov: 10,
            position: [-60, 80, 100],
          }}
        >
          <Suspense fallback={null}>
            <Building />
            <City />
            <OrbitControls
              enablePan
              enableZoom
              enableRotate
              enableDamping
              minPolarAngle={0}
              maxPolarAngle={Math.PI * 0.5}
            />
          </Suspense>
          <directionalLight intensity={1} />
          <ambientLight intensity={3} castShadow />
        </Canvas>
        <FloorOverview />
      </div>
    </div>
  );
}
