/* eslint-disable no-param-reassign */
import { createSlice } from '@reduxjs/toolkit';

export const homePopupState = createSlice({
  name: 'homePopupState',
  initialState: {
    value: false,
  },
  reducers: {
    setHomePopupTrue: (state) => {
      state.value = true;
    },
    setHomePopupFalse: (state) => {
      state.value = false;
    },
    homePopupByState: (state, action) => {
      state.value = action.payload;
    },
  },
});

export const {
  setHomePopupTrue,
  setHomePopupFalse,
  homePopupByState,
} = homePopupState.actions;

export default homePopupState.reducer;
